import { Component, OnInit, ViewChild, OnDestroy, AfterViewInit, AfterViewChecked } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, StorageService, Constants } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { PasswordComponent } from 'src/app/shared/password-popup/password.component';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { MOMENT_DATA, SHOW_PASSWORD_MODAL } from 'src/app/utils/utils';
import { EVENTS, TIMER, INITIALIZE_TIMER, } from 'src/app/utils/constants';

@Component({
  selector: 'ism-moments',
  templateUrl: './moments.component.html',
  styleUrls: ['./moments.component.styl']
})
export class MomentsComponent implements OnInit, OnDestroy, AfterViewInit {
  modelChangeSubscriber:Subscription;
  MomentsData;  
  isSubmitted = false;
  enableSubmit=false;

  activeTabIndex = 0;
  avoidAnimation = true;
  @ViewChild('passwordModal', {static: false}) public passwordContentRef: PasswordComponent;

  constructor(
    private textEngineService: TextEngineService,
    protected calcService: CalcService,
    private communicator: CommunicatorService,
    private router: Router,
    private storageService: StorageService
  ) { }

  title;

  ngOnInit() {
    this.getData();
    this.modelChangeSubscriber=this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe((res) => {
      this.onModelChange();
    });
  }

  ngAfterViewInit(){
    setTimeout(() => {
      this.avoidAnimation = false;
    }, 2000);
  }

  getData(){
    let MomentsDataObj = this.textEngineService.getInitiatives('Moments')
    this.MomentsData =  Object.keys(MomentsDataObj).map((key) => MomentsDataObj[key]);

    this.onModelChange();
    
    this.storageService.getValue(EVENTS.MOMENTS_COMPLETE).then((isMomentCompleted)=>{
      if(isMomentCompleted == "true"){
        this.isSubmitted = true;
        this.communicator.trigger(INITIALIZE_TIMER, null)
      }else{
        this.isSubmitted = false;
        this.communicator.trigger(INITIALIZE_TIMER, TIMER.MOMENTS_TIMER)
      }
    })
  }

  onModelChange() {
    // Check if it has been triggered otherwise show locked and disabled
    for(var i=0;i< this.MomentsData.length;i++){
      const curVal = this.calcService.getValue(this.MomentsData[i].triggerRef);
        this.MomentsData[i].enabled=curVal=="1";
    }

    // Check if all submitted
    let count=0;
    for(var i=0;i< this.MomentsData.length;i++){
      const curVal = this.calcService.getValue(this.MomentsData[i].readRef);
        if(curVal=="1"){
          count++;
        };
    }
    if(count==this.MomentsData.length){
      this.enableSubmit=true;
    }

  }
  submit(){
    // this.communicator.trigger(SHOW_PASSWORD_MODAL);
    this.passwordContentRef.showModal();
  }

  navigateByUrl(){
    if(!this.isSubmitted){
      this.storageService.setValue(EVENTS.MOMENTS_COMPLETE, "true");
      this.calcService.setValue('tlInputMomentsCompleted', 1);
  
    }
    this.router.navigate(['dashboard', 'pnl']);
  }

  ngOnDestroy(){
    if(this.modelChangeSubscriber){
      this.modelChangeSubscriber.unsubscribe()
    }
  }

  onNext(){
    if(this.activeTabIndex != this.MomentsData.length -1){
      this.setActiveTabIndex(this.activeTabIndex+1)
    }else{
      // Continue for Last moment
      if(this.isSubmitted){
        this.navigateByUrl()
      }else{
        this.submit()
      }
    }
  }

  setActiveTabIndex(index){
    this.activeTabIndex = index
  }

}
