import { Component, OnInit, Input, Output, EventEmitter, ElementRef, AfterViewInit } from '@angular/core';
import * as Rx from 'rxjs';
import * as _ from 'lodash';
import {fromEvent, Subscription} from 'rxjs';
import { CalcService, Constants, TextService, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from '../../shared/textengine/textengine.service';

@Component({
  selector: 'ism-selection',
  templateUrl: './selection.component.html',
  styleUrls: ['./selection.component.styl']
})
export class SelectionComponent implements OnInit {

  @Input() SelectionType: string;
  @Input() SelectionData: any;
  @Output() Selected = new EventEmitter();
  private currentSelectedIvestments: Array<any> = [];
  private selectedInvestmentsOnCheck: Array<any> = [];
  private subscriber: Subscription;

  list = false;
  optionSelected: any = [];
  investmentsArray: any = [];
  selectionArray: any = [];
  content: any;
  maxSelection: number;
  displayOptionCount: number;
  navigationFlag: string;
  private isChecked = 0;

  private maxChecks = 4;
  private totalChecked = 0;
  private currentYear = this.calcservice.getValue('xxRound');
  private currentPhase: number = 1;

  el: HTMLElement;

  constructor(private textengineService: TextEngineService, public calcservice: CalcService, private elRef: ElementRef, private isomerXapiService: IsomerXapiService) {
    const source = fromEvent(document, 'click');
    source.subscribe((d: any) => {
      const clickedInside = this.elRef.nativeElement.contains(d.target);
      if (!clickedInside) {
        this.list = false;
      }
    });
  }

  ngOnInit() {

    if (!this.SelectionData) {
      return;
    }

    this.subscriber = this.calcservice.getObservable().subscribe(() => {
    })
    for (let i = this.currentPhase == 1 ? 1 : 5; i <= this.maxChecks; i++) {
      //track data on load
      this.isomerXapiService.trackDataOnLoad(this.calcservice.getWorksheetName('tlInputSI' + i), 'tlInputSI' + i, String(0), this.currentYear, 'stratergicinitiatives', false);
    }

    this.currentYear = this.calcservice.getValue('xxRound');
    this.initializeContent();

    this.updateCheckedInititiatives();


    /**
     * Add selected property to short selection data
     */
    if (this.SelectionData.type === 'short' || this.SelectionData.type === 'long') {
      this.investmentsArray.forEach(element => {
        element.selected = false;
        element.clickTitle = false;
      });
    }

    /**
    * Chunk for long selection
    */
    if (this.SelectionData.type === 'long') {
      this.displayOptionCount = this.investmentsArray.length;
      this.investmentsArray = _.chunk(this.investmentsArray, 8);
      this.optionSelected = this.investmentsArray[0][0];
      // this.SelectionData.options = this.investmentsArray;
      /* By default 1st option selected */
      //this.SelectionData.options[0][0].selected = true;
      // this.SelectedValue(this.SelectionData.options, null);
      // this.shortOptionSelect(this.SelectionData.options[0][0]);
    }

  }

  droplist(event) {
    event.stopPropagation();
    this.list = !this.list;
  }

  submitValues() {
    this.setNullValues();
    for (let i = 0; i < this.currentSelectedIvestments.length; i++) {
      let selectedInit = this.currentSelectedIvestments[i];
      let selected = selectedInit.initiativeID;
      let index = this.currentPhase == 1 ? (i + 1) : (i + 5);

      //use below loop if submitvalues method call in oncheck method instaed of submit button.
      if (this.selectedInvestmentsOnCheck[0] && this.selectedInvestmentsOnCheck[0].initiativeID == selected) {
        this.calcservice.setValue('tlInputSI' + index, selected, false, String(this.currentYear), 'stratergicinitiatives');
      }
      else {
        this.calcservice.setValue('tlInputSI' + index, selected, false, String(this.currentYear));
      }
      //use below code when submitvalues method call on  submit button

      //this.calcservice.setValue('tlInputSI' + index, selected, false, String(this.currentYear), 'stratergicinitiatives');

    }
  }

  setNullValues() {
    let maxcheck = this.currentPhase == 1 ? 4 : 10;
    for (let i = this.currentPhase == 1 ? 1 : 5; i <= maxcheck; i++) {
      // if initiative was checked this year
      this.calcservice.setValue('tlInputSI' + i, '0', false, String(this.currentYear));
    }
  }

  initializeContent() {
    this.content = this.textengineService.getInitiatives();


    var arr = [];
    for (var index in this.content) {

      this.content[index].isSelected = 0;
      this.content[index].isChecked = 0;
      this.content[index].isEnabled = 1;

      // break apart .ID for true ID (eg - Initiative1 = 1)
      this.content[index].initiativeID = this.content[index].ID.slice(10);

      this.investmentsArray.push(this.content[index]);
    }


    for (let i = 0; i < this.investmentsArray.length; i++) {
      this.investmentsArray[i].isEnabled = (
        ((this.investmentsArray[i].isChecked == this.currentYear) ||
          ((this.investmentsArray[i].isChecked == 0) && (this.totalChecked < this.maxChecks))));
      // this.checkInitiativeState(i);

    }
    if (this.SelectionData.type === 'long') {
      this.selectionArray = this.investmentsArray;
      // this.investmentsArray = _.chunk(this.investmentsArray, 8);
      // this.displayOptionCount = this.investmentsArray.length;
      // this.SelectionData.options = this.investmentsArray;
      // /* By default 1st option selected */
      // // this.SelectionData.options[0][0].isSelected = true;
      // this.optionSelected = this.investmentsArray[0][0];
    }
  }

  updateCheckedInititiatives() {
    this.totalChecked = 0;
    let status: number = 0;
    let currentYear = this.calcservice.getValue('xxRound');
    let last = this.currentPhase == 1 ? 4 : 10;

    for (let i = 1; i <= last; i++) {
      // if initiative was checked this year
      status = parseInt(this.calcservice.getValue('tlInputSI' + i));
      if (status) {
        this.totalChecked++;
        for (var index in this.selectionArray) {
          if (this.selectionArray[index].initiativeID == status) {
            this.selectionArray[index].isChecked = currentYear;
            this.selectionArray[index].isSelected = currentYear;
            this.selectionArray[index].clickTitle = currentYear;
            // this.selectionArray[index].isChecked = currentYear;
            this.maxSelection = this.totalChecked;
          }
        }
      }
    }


  }

  SelectedValue(val, event, type?: string) {
    this.SelectionData.labelInActive = val.ID;
    if (event !== null && type !== 'droplist') {
      event.stopPropagation();
    }

    this.shortOptionSelect(val);

    if (this.SelectionData.type === 'short') {
      this.selectionArray = this.investmentsArray;
    }
    val.isSelected = !val.isSelected;
    const index = this.selectionArray.indexOf(val);
    // if (!val.isSelected) {
    //   const index = this.selectionArray.indexOf(val);
    //   this.setVal(index + 1, '0', String(this.currentYear));
    // }

    // if (val.isSelected) {
    //   const index = this.selectionArray.indexOf(val);
    //   this.setVal(index + 1, String(val.initiativeID), String(this.currentYear));
    // }


    // const tempArray = [];
    // Rx.Observable.from(this.investmentsArray)
    //   .flatMap((f: any) => f)
    //   .filter((d: any) => {
    //     return d.isSelected === true;
    //   }).subscribe(data => {

    //     tempArray.push(data);
    //   });

    // check/uncheck initiative
    if (this.selectionArray[index].isEnabled) {
      this.selectionArray[index].isChecked = (this.selectionArray[index].isChecked > 0 ? 0 : this.currentYear);
      this.selectedInvestmentsOnCheck.length = 0;
      this.selectedInvestmentsOnCheck.push(this.selectionArray[index]);
      this.totalChecked = 0;
      this.currentSelectedIvestments.length = 0;
      for (let i = this.currentPhase == 1 ? 0 : 10; i < this.selectionArray.length; i++) {
        // if initiative was checked this year
        if (this.selectionArray[i].isChecked == this.currentYear) {
          this.totalChecked++;

          this.currentSelectedIvestments.push(this.selectionArray[i]);
        }
      }

      for (let i = 0; i < this.selectionArray.length; i++) {
        this.selectionArray[i].isEnabled = (
          ((this.selectionArray[i].isChecked == this.currentYear) ||
            ((this.selectionArray[i].isChecked == 0) && (this.totalChecked < this.maxChecks))));
        // this.investmentsArray[index].isSelected = !this.investmentsArray[index].isSelected;

        // this.checkInitiativeState(i);
      }
    }


    this.Selected.emit(this.currentSelectedIvestments);
    this.maxSelection = this.currentSelectedIvestments.length;
    this.submitValues();
    // }


  }

  shortOptionSelect(data) {
    if (this.SelectionData.type === 'long') {
      this.investmentsArray.forEach(element => {
        element.forEach(ele => {
          ele.clickTitle = false;
        });
      });
      data.clickTitle = !data.clickTitle;
      this.optionSelected = data;
    }
    // else {
    //   const index = this.investmentsArray.indexOf(data);
    //   this.investmentsArray[index].isSelected = !this.investmentsArray[index].isSelected;
    //   this.maxSelection = this.investmentsArray.filter(f => f.isSelected === true).length;
    //   // this.calcservice.setValue('tlInputSI' + (index + 1), this.investmentsArray[index].initiativeID, false, String(this.currentYear), 'selection');
    //   // this.setVal(index + 1, String(this.investmentsArray[index].initiativeID), String(this.currentYear));
    // }

  }

  displayOptions(flag) {
    this.navigationFlag = flag;
    return flag;
  }




}
