import { Component, OnInit, ViewChild, OnDestroy, AfterViewInit } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CalcService, CommunicatorService, StorageService, Constants } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { PasswordComponent } from 'src/app/shared/password-popup/password.component';
import { MOMENT_DATA } from 'src/app/utils/utils';
import { EVENTS, SCENARIO_SUBMIT } from 'src/app/utils/constants';

@Component({
  selector: 'ism-scenarios',
  templateUrl: './scenarios.component.html',
  styleUrls: ['./scenarios.component.styl']
})
export class ScenariosComponent implements OnInit, OnDestroy, AfterViewInit {
  modelChangeSubscriber: Subscription;
  ScenariosData;
  isSubmitted = false;
  enableSubmit = false;

  activeTabIndex = 0;
  avoidAnimation = true;
  // @ViewChild('passwordModal', { static: false }) public passwordContentRef: PasswordComponent;

  constructor(
    private textEngineService: TextEngineService,
    protected calcService: CalcService,
    private communicator: CommunicatorService,
    private router: Router,
    private storageService: StorageService
  ) { }  

  ngOnInit() {
    this.getData();
    this.modelChangeSubscriber = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe((res) => {
      this.checkStatus();
    });
    
    if(sessionStorage.getItem('selectedTab')){
      this.activeTabIndex = parseInt(sessionStorage.getItem('selectedTab'));
    }
  }

  ngAfterViewInit(){
    setTimeout(() => {
      this.avoidAnimation = false;
    }, 2000);
  }

  getData() {
    let ScenariosDataObj = this.textEngineService.getInitiatives('Scenario')
    this.ScenariosData = Object.keys(ScenariosDataObj).map((key) => ScenariosDataObj[key]);

    this.checkStatus();

    this.storageService.getValue(EVENTS.SCENARIO_COMPLETE).then((isScenarioCompleted) => {
      if (isScenarioCompleted == "true") {
        this.isSubmitted = true
      } else {
        this.isSubmitted = false;
      }
    })
  }

  checkStatus() {
    // Check if it has been triggered otherwise show locked and disabled
    for (var i = 0; i < this.ScenariosData.length; i++) {
      const curVal = this.calcService.getValue(this.ScenariosData[i].triggerRef);
      this.ScenariosData[i].enabled = curVal == "1";
    }


    // Check if all submitted
    let count = 0;
    for (var i = 0; i < this.ScenariosData.length; i++) {
      const curVal = this.calcService.getValue(this.ScenariosData[i].readRef);
      if (curVal == "1") {
        count++;
      };
    }
    if (count == this.ScenariosData.length) {
      this.enableSubmit = true;
    }

  }

  submit() {
    this.communicator.trigger(SCENARIO_SUBMIT)
  }

  navigateByUrl() {
    if (!this.isSubmitted) {
      this.storageService.setValue(EVENTS.SCENARIO_COMPLETE, "true")
    }
    this.router.navigate(['dashboard', 'pnl']);
  }

  ngOnDestroy(){
    if(this.modelChangeSubscriber){
      this.modelChangeSubscriber.unsubscribe()
    }
  }

  onNext(){
    if(this.activeTabIndex != this.ScenariosData.length -1){
      this.setActiveTabIndex(this.activeTabIndex+1)
    }else{
      // Continue for Last moment
      if(this.isSubmitted){
        this.navigateByUrl()
      }else{
        this.submit()
      }
    }
  }

  setActiveTabIndex(index){
    this.activeTabIndex = index;

    sessionStorage.setItem('selectedTab', JSON.stringify(index));
  }

}

