import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { TextService, CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
    selector: 'ism-calc-textarea',
    templateUrl: './calc-textarea.html',
    styleUrls: ['./calc-textarea.styl'],
    providers: []
})

export class CalcTextareaComponent implements OnInit {
    content: string = "";
    text: string;
    private isDirty: boolean = false;
    inputRow: number;
    @Input() ref: string = "";
    @Input() textareaRows: number = 3;
    @Input() isReadOnly: boolean = false;
    @Input() placeholderText: string = 'Enter response here...';
    subscription: Subscription;
    
    constructor(private textEngineService: TextService, private calcService: CalcService, private router: Router) { };

    ngOnInit() {
       this.update()
        this.inputRow = this.textareaRows;

        // if(this.isReadOnly){
        //     this.subscription = this.calcService.getObservable().subscribe(() => {
        //        this.update()
        //     });
        // }
    }

    update(){
        try{
            this.content = this.calcService.getValue(this.ref);
        }catch(e){}
        this.text = this.content == "0" ? '' : this.content;
    }

    onBlur() {
        // this.calcService.setValue(this.ref, this.content);
        if(this.isReadOnly){
            return;
        }

        if(this.text == ''){
            this.content = '0';
        }else{
            this.content = this.text;
        }
        
        if(this.isDirty){
            if (this.content.match(/^\d/)) {
                this.content = '`' + this.content;
            }
            this.calcService.setValue(this.ref, this.content);
            this.isDirty = false;
        }
    }

    onModelChange(val: any) {
        if (val === '') {
            return;
        }
        this.text = val;
        this.isDirty = true;
    }

    ngOnDestroy() {
        if(this.subscription){
            this.subscription.unsubscribe();
        }
    }
}
