import { Component, OnInit, TemplateRef, Input, ViewChild, ViewContainerRef } from '@angular/core';
import { BsModalRef, BsModalService, ModalOptions } from 'ngx-bootstrap/modal';

@Component({
  selector: 'ism-common-modal',
  templateUrl: './common-modal.component.html',
  styleUrls: ['./common-modal.component.styl']
})
export class CommonModalComponent implements OnInit {
  commonModalRef: BsModalRef;
  @Input() className = 'modal-lg '
  config: ModalOptions;
  @Input() modalContentRef: TemplateRef<any>;

  constructor(
    public modalService: BsModalService,
  ) { }

  ngOnInit() {
    this.config = {
      class: 'gray '+this.className,
      ignoreBackdropClick: true,
      focus: true,
      keyboard: false
    }
  }

  openModal(template: TemplateRef<any>) {
    this.commonModalRef = this.modalService.show(
      this.modalContentRef, this.config
    );
  }

  closeModal() {
    this.commonModalRef.hide()
  }

}
