import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ViewsModule } from './views/views.module';
import { SharedModule } from './shared/shared.module';
import { ManifestService, HttpWrapperService } from '@btsdigital/ngx-isomer-core';

import { SignalRService } from '@btsdigital/pulsesignalr';

import { AppConfig } from './app.config';
import { ModalModule } from 'ngx-bootstrap/modal';
import { VideoCallManagerService } from '@btsdigital/pulsewebrtc';

import { BsModalRef } from 'ngx-bootstrap/modal';
import { CountdownComponent } from 'ngx-countdown';
import { ImportExportModule } from './import-export/import-export.module';

export let browserRefresh = false;

@NgModule({
  declarations: [
    AppComponent,

  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    // IsomerCoreModule,
    ViewsModule,
    BrowserAnimationsModule,
    // SharedModule,
    SharedModule,

    // ParticipantAppAuthoringModule
    ModalModule.forRoot(),
    ImportExportModule
  ],
  providers: [BsModalRef, AppConfig, 
    {
      provide: APP_INITIALIZER,
      useFactory: loadConfig,
      deps: [
        AppConfig
      ],
      multi: true
    }
  ],
  bootstrap: [AppComponent],

})
export class AppModule { }

export function loadConfig(config: AppConfig) {

  return () => {
    const promise = config.load().toPromise();
    promise.then(r => {
      config.setConfig = r;
    });

    return promise;
  };
}

