import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { DataService } from 'src/app/shared/dataService';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'ism-profit-loss',
  templateUrl: './profit-loss.component.html',
  styleUrls: ['./profit-loss.component.styl']
})
export class ProfitLossComponent implements OnInit {
  additionalClass = '';
  percSalesState: boolean = false;

  constructor(private textEngineService: TextEngineService, protected calcService: CalcService,
    private communicator: CommunicatorService, private router: Router, private dataService: DataService) {

}

  header=[];
  header1=[];
  header2=[];
  header3=[];
  header4=[];
  header5=[];
  header6=[];
  tableData1=[];
  tableData2=[];
  tableData3=[];
  tableData4=[];
  tableData5=[];
  tableData6=[];
  sales;

  showpnl1 = "showpnl1";
  showpnl2 = "showpnl2";
  showpnl3 = "showpnl3";
  showpnl4 = "showpnl4";
  showpnl5 = "showpnl5";
  showpnl6 = "showpnl6";

  pnl="pnl";

  ngOnInit() {
    this.getData();
    this.checkToggle();
  }

  getData(){

      this.sales = this.textEngineService.getText('columnpnllabe3')
      this.header =[
        {id:2, name: this.textEngineService.getText('columnpnllabe2')},
        {id:3, name: this.textEngineService.getText('columnpnllabe3')},
        {id:4, name:this.textEngineService.getText('columnpnllabe4')},
        {id:5, name:this.textEngineService.getText('columnpnllabe5')},
        {id:6, name:this.textEngineService.getText('columnpnllabe3')}
      ]
      this.header1=[
        {id:1, name: this.textEngineService.getText('rowPnllabel7')},
        ...this.header
      ];

      this.header2=[
        {id:1, name: this.textEngineService.getText('rowPnllabel11')},
        ...this.header
      ];

      this.header3=[
        {id:1, name: this.textEngineService.getText('rowPnllabel18')},
        ...this.header
      ];

      this.header4=[
        {id:1, name: this.textEngineService.getText('rowPnllabel22')},
        ...this.header
      ];

      this.header5=[
        {id:1, name: this.textEngineService.getText('rowPnllabel26')},
        ...this.header
      ];

      this.header6=[
        {id:1, name: this.textEngineService.getText('rowPnllabel28')},
        ...this.header
      ];

      this.tableData1 = this.dataService.getAccordian1();
      this.tableData2 = this.dataService.getAccordian2();
      this.tableData3 = this.dataService.getAccordian3();
      this.tableData4 = this.dataService.getAccordian4();
      this.tableData5 = this.dataService.getAccordian5();
      this.tableData6 = this.dataService.getAccordian6();
    
  }

  checkToggle(){
    if(this.percSalesState){
      this.additionalClass = ''
    }else{
      this.additionalClass = 'hidePercSales'
    }
  }

  updateToggle(){
    this.percSalesState = !this.percSalesState;
    this.checkToggle();
  }

}
