import { Component, OnInit, ViewChild, Input, Output, EventEmitter, TemplateRef, OnDestroy, HostListener } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal/public_api';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CommonModalComponent } from '../common-modal/common-modal.component';
import { Subscription } from 'rxjs';
import { SHOW_PASSWORD_MODAL } from 'src/app/utils/utils';
import { FORCE_PASSWORD_POPUP } from 'src/app/utils/constants';

const MASTER_PASSWORD = 'bts';
const KEY_NUM_1 = 49;

@Component({
  selector: 'ism-password',
  templateUrl: './password.component.html',
  styleUrls: ['./password.component.styl']
})
export class PasswordComponent implements OnInit, OnDestroy {
  @ViewChild('passwordContentRef', {static: false}) public passwordContentRef: CommonModalComponent;
  @ViewChild('passwordContent', {static: false}) public passwordContent: TemplateRef<any>;
  @Input() messageKey = 'Enter the Password';
  @Input() passwordKey = '';
  @Input() hideCancel = false;
  passwordInput: any;
  @Output() Authenticated: EventEmitter<any> = new EventEmitter();
  password = '';
  wrongPassword = false;
  forcePopupSubsriber: Subscription;
  isModalOpen :Boolean = false;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_1) {
      this.toggleCancelButton();
    }
  }

  constructor(private textEngineService: TextEngineService,protected calcService: CalcService, private communicator: CommunicatorService) { }

  ngOnInit() {
    this.forcePopupSubsriber  =this.communicator.getEmitter(FORCE_PASSWORD_POPUP).subscribe(()=>{
      this.hideCancel = true;
      this.showModal();
    })
  }

  ngOnDestroy(){
    if(this.forcePopupSubsriber){
      this.forcePopupSubsriber.unsubscribe();
    }
  }

  showModal() {
    if(this.isModalOpen){
      return 
    }
    this.password = '';
    this.wrongPassword = false;
    this.isModalOpen = true;
    this.passwordContentRef.openModal(this.passwordContent);
  }

  hideModal() {
    this.passwordContentRef.closeModal();
    this.isModalOpen = false;
    // this.password = '';
    // this.wrongPassword = false;
  }

  onConfirmClicked() {
    const passwordValue = this.textEngineService.getText(this.passwordKey);
    if (passwordValue == this.password || this.password == MASTER_PASSWORD) {
      this.hideModal();
      this.Authenticated.emit();
      this.hideCancel = false;      
    } else {
      this.wrongPassword = true;
      console.log('password incorrect');
    }
  }

  toggleCancelButton(){
    if(this.isModalOpen){
      this.hideCancel = !this.hideCancel
    }
  }

}
