import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { TextEngineService } from '../../shared/textengine/textengine.service';

@Component({
  selector: 'isma-info-update',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.styl']
})
export class InfoComponent implements OnInit {

  @Input() data: any;
  @Input() rangeref: string;

  id: string;
  scene: any;

  @Output() continue: EventEmitter<any> = new EventEmitter<any>();

  constructor(private calcService: CalcService, private textHelper: TextEngineService) { }

  ngOnInit() {
    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data.scene;
    }
  }

  onSubmit(){
    this.calcService.setValue(this.rangeref, 1);
  }
}
