import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

const KEY_NUM_3 = 51;

@Component({
  selector: 'isma-calc-debugger',
  templateUrl: './calc-debugger.component.html',
  styleUrls: ['./calc-debugger.component.styl']
})
export class CalcDebuggerComponent implements OnInit {
  isCalcDebuggerActive: boolean = false;
  @ViewChild('calcDebuggerModal', {static: true}) calcDebuggerModalRef: ModalDirective;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_3) {
      this.showCalcDebugger();
    }
  }

  constructor() { }

  ngOnInit() {
  }

  showCalcDebugger(){
    this.isCalcDebuggerActive = true;
    this.calcDebuggerModalRef.show();
  }

  hideCalcDebugger(){
    this.calcDebuggerModalRef.hide();
    this.isCalcDebuggerActive = false;
  }

}
