import { Component, OnInit, Input, Renderer, ElementRef } from '@angular/core';
import { CalcService,  TextService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';

@Component({
    selector: 'my-tooltip',
    templateUrl: './tooltip.html',
    styleUrls: ['./tooltip.styl']
})

export class Tooltip implements OnInit {
    private tooltipClass: string = 'tooltipHide';
    @Input() tooltipPlacement: string = '';
    @Input() tooltipText: string = '';
    @Input() key: string = '';
    @Input() keyRef: string = '';
    constructor(private textEngineService: TextService, private calcService: CalcService, private router: Router) { }
    
    ngOnInit() {
        this.getData();
    }

    getData(){
        if (this.key) {
            let tooltipText = this.key;
            this.tooltipText = tooltipText;
            }
        }
    }
