import { Component, OnInit, ViewChild, ComponentFactoryResolver, Input } from '@angular/core';
import { ContentHostDirective } from '../../shared/content-host.directive';
import { Subscription } from 'rxjs';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { DataService } from 'src/app/shared/dataService';
import { PAYROLL_NAVIGATE } from 'src/app/utils/utils';
import { EVENTS } from 'src/app/utils/constants';

@Component({
  selector: 'ism-accordian',
  templateUrl: './accordian.component.html',
  styleUrls: ['./accordian.component.styl']
})
export class AccordianComponent implements OnInit {

  @Input() headerData: any = [];
  @Input() tableData: any = [];
  @Input() id: any;
  @Input() isDisable: Boolean = false;
  @Input() accordianValue = "";
  @Input() scenarioClass = "";
  @Input() tableName;
  @Input() noTotalRow = false;
  totalData = [];
  openAccordian = [];

  constructor(private textEngineService: TextEngineService, protected calcService: CalcService,
    private communicator: CommunicatorService, private router: Router, private dataService: DataService) { }

  showAccordian: Boolean = true;

  ngOnInit() {
    if (!this.noTotalRow) {
      this.getTotalRowData();
    }
    this.showOpenedAccordian();
  }

  showOpenedAccordian() {
    let value = sessionStorage.getItem(this.accordianValue);
    if (value) {
      this.showAccordian = JSON.parse(value)
    }
  }

  getTotalRowData() {
    let obj = this.tableData.pop()
    this.totalData.push(obj);
  }

  toggleAccordian(value) {
    this.showAccordian = !value;
    sessionStorage.setItem(this.accordianValue, JSON.stringify(this.showAccordian))
  }

  navigate(value) {
    console.log(value);
    let data = value.toLowerCase() == 'payroll' ? 'payroll' : 'pnl';
    this.router.navigate(['dashboard', data]);
    this.communicator.trigger(PAYROLL_NAVIGATE, 1);
  }

  ngOnDestroy(){
  }

}
