import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, Constants, HttpWrapperService, ManifestService, StorageService, SyncService, TextService } from '@btsdigital/ngx-isomer-core';
import { SignalRService } from '@btsdigital/pulsesignalr';
import { LoggerService, XApiService, XApi } from '@btsdigital/pulseutilities';
import { VideoCallManagerService } from '@btsdigital/pulsewebrtc';
import { NotifyService } from '../custom-comp/notification/notify.component';
import { ModelLoaderService } from '../model/model-loader.service';
import { IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from './textengine/textengine.service';
import { APP_READY } from '../utils/constants';
import { AppConfig } from '../app.config';

// set theme mode
window['__theme'] = 'bs4';
@Injectable()
export class BootstrapService {

  // private modelLoadProgressSub: Subscription;
  private textServiceInitPromise: Promise<any>;
  private textEngineServiceInitPromise: Promise<any>;  
  private modelLoadCompletePromise: Promise<any>;
  private isAppReady = false;
  private appConfigInitialized = false;
  userObj: any;
  // private testNotify = new notifyComponent();
  constructor(private calcService: CalcService, private textService: TextService, private textEngineService: TextEngineService,
    private storageService: StorageService, private httpWrapper: HttpWrapperService, private appConfig: AppConfig,
    private communicator: CommunicatorService, private logger: LoggerService, private videoCallManager: VideoCallManagerService,
    private router: Router, private modelLoader: ModelLoaderService, private signalR: SignalRService, private syncService: SyncService,
    private testNotify: NotifyService, private manifestService: ManifestService, private httpWrapperService: HttpWrapperService, private xpiService: XApiService, private isomerXapiService: IsomerXapiService) {
    // added user data and nodeservice url for tracking
    this.userObj = JSON.parse(localStorage.getItem('user'));
    this.xpiService.configureAngularticsUrl(appConfig.configuration.nodejsUrl);
  }

  initAppConfig() {
    this.logger.isLogging = this.appConfig.configuration.logging;
    const SERVER_ZONE = window.localStorage.getItem('ManifestHost') || undefined;
    // environment.hostname = (zoneMatch.length) ? 'cisco-zone' + SERVER_ZONE_NUMBER + '.btspulse.com' : environment.hostname;
    if(SERVER_ZONE){
      this.appConfig.configuration.hostName = SERVER_ZONE;//environment.hostnames[Number(SERVER_ZONE_NUMBER) - 1];
      this.appConfig.configuration.serviceUrl = SERVER_ZONE + '/PulseServices/';//environment.hostnames[Number(SERVER_ZONE_NUMBER) - 1];
    }
    
    this.manifestService.setConfig({ config: this.appConfig.configuration });
    this.httpWrapperService.setHostName(this.appConfig.configuration.hostName);
    this.signalR.configureSignalRService({ hostname: this.appConfig.configuration.hostName, serviceUrl: this.appConfig.configuration.serviceUrl, hasHeartBeat: true });
    this.videoCallManager.configureWebRTCService({ serviceUrl: this.appConfig.configuration.serviceUrl, eject: false });
    this.isomerXapiService.configureIsomerAppService({
      enabletracking: this.appConfig.configuration.enableTracking, statusCompletionQuestions: this.appConfig.configuration.statusCompletionQuestions,
      rangeRefTags: this.appConfig.configuration.rangeRefTags, enableLoadLevelTracking: this.appConfig.configuration.enableLoadLevelTracking
    });

    // set the user for tracking
    if (this.appConfig.configuration.enableTracking) {
      try {
        if (this.userObj) {
          this.xpiService.setUserProperties(this.userObj.pid, this.userObj.pname, this.userObj.pemail, this.userObj.planguagekey,
            this.userObj.token, this.userObj.moduleName, this.userObj.userExperience, this.appConfig.configuration.hostName,
            this.appConfig.configuration.activityType,
            this.userObj.userExperience, this.appConfig.configuration.platform,
            this.appConfig.configuration.defaultEmail, this.appConfig.configuration.lrsUrl, this.appConfig.configuration.serviceUrl, this.appConfig.configuration.enableEmailEncryption);

        }
      } catch (e) {
        this.logger.log('Error initializing UserProperties: ' + e);
      }
    }

    // set storage mode
    this.storageService.setMode(this.appConfig.configuration.connectToPulse ? Constants.STORAGE_MODES.MIXED : Constants.STORAGE_MODES.LOCAL);

    this.appConfigInitialized = true;
  }

  init(): Promise<any> {
    if (!this.appConfigInitialized) {
      this.initAppConfig();
    }
    return this.modelLoader.getModel().then((response) => {
      const model = response;

      // set initial language to be loaded
      this.textService.language = 'en';
      this.testNotify.notificationData['flag'] = true;
      this.testNotify.notificationData['message'] = 'Model Loaded Successfully';
      this.testNotify.notificationData['display'] = true;


      // load text content
      this.textServiceInitPromise = this.textService.init();
      this.textEngineServiceInitPromise = this.textEngineService.init();
      // try and load the calc model
      this.modelLoadCompletePromise = this.calcService
        .getApi(model)
        .catch((err) => {
          this.logger.log('Error when loading model ', err);
          this.testNotify.notificationData['flag'] = false;
          this.testNotify.notificationData['message'] = err;
          this.testNotify.notificationData['display'] = true;
        });

      return Promise.all([this.textServiceInitPromise, this.modelLoadCompletePromise,this.textEngineServiceInitPromise])
        .then(() => {
          if (this.appConfig.configuration.connectToPulse) {
            return this.syncService.initializeSync().then(() => {
              return this.syncService.setMode(Constants.CONNECTION_MODE.PUSH).then(() => {
                this.appReady = true;
              });
            });
          } else {
            this.appReady = true;
          }
        });
    });
  }

  public set appReady(v: boolean) {
    this.isAppReady = v;
    this.communicator.trigger(APP_READY, v);
  }

  public get appReady(): boolean {
    return this.isAppReady;
  }
}
