import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LogoutComponent } from './shared/logout/logout.component';
import { SplashComponent } from './shared/splash/splash.component';
import { IntroComponent } from './views/intro/intro.component';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { DashboardComponent } from './views/dashboard/dashboard.component';
import { InitiativesComponent } from './views/initiatives/initiatives.component';
import { HoldScreenComponent } from './views/hold-screen/hold-screen.component';
import { SampleDecisionsComponent } from './sample-decisions/sample-decisions/sample-decisions.component';
import { DashboardDataComponent } from './views/dashboard-data/dashboard-data.component';
import { IdeaHuntComponent } from './views/idea-hunt/idea-hunt.component';
import { MomentsComponent } from './views/moments/moments.component';
import { ProfitLossComponent } from './views/profit-loss/profit-loss.component';
import { PayrollComponent } from './views/payroll/payroll.component';
import { ScenarioComparisonComponent } from './views/scenario-comparison/scenario-comparison.component';
import { ScenariosComponent } from './views/scenarios/scenarios.component';
import { PdfGenComponent } from './views/pdf-gen/pdf-gen.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/splash',
    pathMatch: 'full'
  },
  {
    path: 'splash',
    component: SplashComponent
  },
  {
    path: 'logout',
    component: LogoutComponent
  },
  {
    path: 'sampledecisions',
    component: SampleDecisionsComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'initiatives',
    component: InitiativesComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'dashboard',
    component: DashboardDataComponent,
    canActivate: [CalcModelLoadedGuard],
    children: [
      {
        path: '',
        redirectTo: 'pnl',
        pathMatch: 'full'
      },
      {
        path: 'pnl',
        component: ProfitLossComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'payroll',
        component: PayrollComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'scenario-comparison',
        component: ScenarioComparisonComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'scenarios',
        component: ScenariosComponent,
        canActivate: [CalcModelLoadedGuard]
      }
    ]
  },
  {
    path: 'holdscreen',
    component: HoldScreenComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'intro',
    component: IntroComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'idea-hunt',
    component: IdeaHuntComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'moments',
    component: MomentsComponent,
    canActivate: [CalcModelLoadedGuard]
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
