import { Component, Input, OnInit, Output, EventEmitter, HostListener } from '@angular/core';
import { CalcService, CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs';
import * as _ from 'lodash';
import { TextEngineService } from '../../shared/textengine/textengine.service';
import { MOMENT_DATA, SHOW_PASSWORD_MODAL } from 'src/app/utils/utils';
import { EVENTS } from 'src/app/utils/constants';

const KEY_NUM_2 = 50;
@Component({
  selector: 'isma-single-select',
  templateUrl: './single-select.component.html',
  styleUrls: ['./single-select.component.styl']
})
export class SingleSelectComponent implements OnInit {

  @Input() data: any;
  @Input() rangeRef: string;
  @Input() readRef: any;
  @Input() triggerRef: any;
  @Input() isDisabled = false;
  @Input() reviewObject: any;
  @Input() reviewSceneId: any;

  choices: any[] = [];
  answer = 0;
  reviewAnswer = -1;
  id: string;
  scene: any;
  public readyToSubmit = false;
  momentScenario;
  showData: Boolean = false;

  @Output() selectedAnswer: Subject<any> = new Subject<any>();
  @Output() submitted = new EventEmitter()
  @Output() conitnue = new EventEmitter()
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_2) {
      this.toggleDisableState();
    }
  }

  constructor(
    private calcService: CalcService, 
    private textHelper: TextEngineService,
    private communicator: CommunicatorService,
    private textEngineService: TextEngineService,
    private storageService: StorageService
  ) { }

  ngOnInit() {

    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data.scene;
      this.rangeRef = this.data.range;
      this.readRef = this.data.readRef;
      this.data.enabled=true;

      const curVal = this.calcService.getValue(this.rangeRef);

      if (curVal != "0") {
        this.isDisabled= true;
        this.answer = curVal.charCodeAt(0) - 64;
        this.showData = true;        
        this.reviewAnswer=this.answer-1;
          // Answer is stored as A,B,C in model where as used as 1,2,3,... in html/ts
      }

      this.choices = this.textHelper.getSingleSelectChoices(this.id);
    }
  }

  onSelect($event) {
    this.readyToSubmit = true;
    //In case some component needs to listen to change
    this.selectedAnswer.next(this.answer);
  }

  onSubmit(){
    if(this.isDisabled !=true){
      this.showData = true;
      this.isDisabled= true;
      this.reviewAnswer=this.answer-1;  
     // this.storageService.setValue(EVENTS.SCENARIOS_COMPLETE, "true")
      this.calcService.setValue(this.readRef, 1)
      this.calcService.setValue(this.rangeRef, String.fromCharCode(64 + (+this.answer))).then((obj)=>{

      }).catch((err)=>{
        console.log(err);
      });
      
      this.submitted.emit()
    }
  }

  onContinue(){
    this.conitnue.emit();
  }

  onKeyPress(e: KeyboardEvent, index){
    switch(e.keyCode){
      case 32:
      case 13:
        this.answer = index
        this.onSelect(null)
    }
  }

  toggleDisableState(){
    if(this.isDisabled && this.showData){
      this.isDisabled = false;
      this.showData = false;
    }
  }
}
