import { Component, OnInit, Input, ViewEncapsulation, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CalcService, TextService, CommunicatorService, StorageService, Constants } from '@btsdigital/ngx-isomer-core';
import { EVENTS, ZONE_INFO } from '../../utils/constants';

const INITIAL_VALUE = '0';

@Component({
  selector: 'ism-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.styl'],
})

export class IntroComponent implements OnInit, OnDestroy {
  currentYear: any;
  TeamNameValue = '';
  username="";
  password="";
  disableInput= false;
  zone
  zoneName

  constructor(protected calcService: CalcService,
    protected textService: TextService,
    private communicatorService: CommunicatorService,
    private router: Router,
    private storageService: StorageService) {

  }

  ngOnInit() {
    this.zone = window.localStorage.getItem('ManifestHost') // https://dsgmlc.btspulse.com/dsg1
    if(this.zone){
      try{
        this.zone = this.zone.slice(this.zone.lastIndexOf('/') + 1);
        this.zoneName = ZONE_INFO[this.zone];
      }catch(e){}
    }


    this.currentYear = this.calcService.getValue('xxRound');
    //set value of the year
    this.storageService.setValue(Constants.ROUND_VALUE, String(this.currentYear));

    this.TeamNameValue = this.calcService.getValue('tlInputTeamName');
    this.storageService.getValue(EVENTS.INTRO_COMPLETE).then((val) => {
      if ((val == undefined || val != 'true') && this.TeamNameValue == INITIAL_VALUE) {
        this.TeamNameValue = '';
      }

      if(val == "true"){
        this.disableInput = true;
      }
    })

  }

  onIntroSubmitClick() {
    if (!this.disableInput) {
      if (this.TeamNameValue.match(/^\d/)) {
        //In case Team name starts with a number
        this.TeamNameValue = '`' + this.TeamNameValue;
      }
      this.calcService.setValue('tlInputTeamName', this.TeamNameValue, false, String(this.currentYear), 'Intro')
      this.storageService.setValue(EVENTS.INTRO_COMPLETE, 'true');
    }

    this.routeToAppropriatPage();
  }

  routeToAppropriatPage(){
    this.storageService.getValue(EVENTS.IDEA_HUNT_COMPLETE).then((isIdeaHuntCompleted)=>{
      if(isIdeaHuntCompleted == "true"){
        this.storageService.getValue(EVENTS.MOMENTS_COMPLETE).then((isMomentsCompleted)=>{
          if(isMomentsCompleted == "true"){
            this.router.navigate(['dashboard']);
          }else{
            this.router.navigate(['moments']);
          }
        })
      }else{
        this.router.navigate(['idea-hunt']);
      }
    })
  }

  ngOnDestroy() { }
}
