import { Component, Input, OnInit, ViewEncapsulation, HostListener } from '@angular/core';
import { CalcService, ConnectThrottlerService, SyncStatusService, StorageService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { debounceTime } from "rxjs/operators";
import { Router, NavigationEnd } from '@angular/router';
import { RAISE_HELP } from 'src/app/utils/constants';
import { Subscription } from 'rxjs';
declare var $: any;
declare var chrome: any;
const KEYCODE_NUM0 = 48;

@Component({
  selector: 'ism-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl'],
  encapsulation: ViewEncapsulation.None
})

export class DashboardComponent implements OnInit {
  showMenu: boolean = false;
  showHelpButton = false;
  showDashboardMetrics = true;
  routeObserver: Subscription;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEYCODE_NUM0) {
      this.resetSim();
    }
  }
  constructor(
    private calcservice: CalcService,
    public router: Router, private storageService: StorageService,
    private connectThrottler: ConnectThrottlerService,
    private syncStatusService: SyncStatusService,
    private communicator: CommunicatorService
  ) { }



  ngOnInit() {
    // Subscribe to ErrorEmitter for error Codes
    this.connectThrottler.ErrorEmitter.subscribe((err: any) => {
      let errorCode = err.errCode;
      // Error Codes:
      // 101 - Saving decimal values to Option type of question
      // 102 - Saving out of range values to Option type of question
    });
    // Subscribe to Status for Sync Status
    this.syncStatusService.Status.subscribe((status: any) => {
      if (status === 2 || status === 3) {
        // this.openErrorModal();
      }
    });


    this.onRouteChange();
    this.routeObserver = this.router.events.subscribe((val) => {
      if (val instanceof NavigationEnd) {
        this.onRouteChange();
      }
    });

  }

  onRouteChange() {
    this.showDashboardMetrics = this.router.url.indexOf("/dashboard") > -1 && this.router.url.indexOf("/scenario-comparison") == -1 && this.router.url.indexOf("/scenarios") == -1 ? true : false;
  }

  resetSim() {
    // localStorage.clear();
    const confirmResponse = confirm('Do you wish to reset sim progress? You cannot undo this.');
    if (confirmResponse) {
      this.storageService.clearAll();
      if (chrome && chrome.runtime && chrome.runtime.reload) {
        chrome.runtime.reload();
      } else {
        window.location.reload();
      }
    }
  }


  //Hamburger Menu
  // onCLickOfMenu() {
  //   const showMenu = !this.showMenu;
  //   this.showMenu = showMenu;
  // }

  // closeMenu() {
  //   setTimeout(() => {
  //     this.showMenu = false;
  //   }, 500);
  // }

  // openMenu($event) {
  //   this.showMenu = true;
  //   $event.preventDefault();
  //   $event.stopPropagation();
  // }

  showHelpAlert() {
    // this.closeMenu();
    this.communicator.trigger(RAISE_HELP);
  }
}
