import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
// import { CalcService } from '../calcmodule';
import { CalcService, TextService, CommunicatorService, Constants, ManifestService } from '@btsdigital/ngx-isomer-core';
import { LoggerService } from '@btsdigital/pulseutilities';
import { AppConfig } from 'src/app/app.config';
const GENERAL: string = 'GEN';
const FILE_NAME: string = './assets/content/EventContent';
const FILE_EXT: string = '.json';
const DEFAULT_LANG: string = 'en';
const Config = require('./../../../config.json');

@Injectable()
export class TextEngineService extends TextService {
    private textContentA: any;
    private textContentPromise: Promise<any>;
    private languageA: string = DEFAULT_LANG;
    public languageChangeEmitter: EventEmitter<any> = new EventEmitter();
    private _http: HttpClient;

    constructor(http: HttpClient, calcService: CalcService, communicator: CommunicatorService, logger: LoggerService,
        manifestService: ManifestService, private httpService: HttpClient) {
        super(http, calcService, manifestService, logger, communicator);
        this._http = http;
        this.textContentA = {};
        this.textContentA[GENERAL] = {};
    }

    init(language?: string): Promise<any> {
        return this.textContentPromise = new Promise((resolve, reject) => {
            // load the default language file first
            this.loadJson(language).then(json => {
                this.processContentJson(json);
                resolve(this.textContentA);
            });
        });
    }

    loadJson(lang) {
        let self = this;
        lang = (lang) ? lang : Constants.TEXT_ENGINE.DEFAULT_LANG; // if no language is passed use default lang
        let filePath = Constants.TEXT_ENGINE.FILE_PATH + Constants.TEXT_ENGINE.FILE_NAME + '_' + lang + Constants.TEXT_ENGINE.FILE_EXT;
        let remoteFilePath = Config.hostName;
        + '/PulseServices/DeliveryDirectorService.svc/DownloadContentJson/'
            + Constants.TEXT_ENGINE.FILE_NAME + '_' + lang + Constants.TEXT_ENGINE.FILE_EXT;
        return new Promise(function (resolve, reject) {
            self.httpService.get(remoteFilePath, { withCredentials: true }).toPromise().then(function (remoteRes) {
                resolve(remoteRes);
            }).catch(function () {
                self.httpService.get(filePath).toPromise().then(function (response) {
                    try {
                        resolve(response);
                    }
                    catch (err) {
                        reject(err);
                    }
                }).catch(function (err) {
                    throw err;
                });
            });
        });
    }

    loadTextContent(language?: string): Promise<any> {
        language = (language) ? language : '_' + DEFAULT_LANG;
        // let languageFile = FILE_NAME + FILE_EXT;
        const languageFile = FILE_NAME + language + FILE_EXT;
        return this._http.get(languageFile)
            .toPromise()
            .then(response => {
                return response;
            })
            .catch(this.handleErrors);
    }

    handleErrors(response: any) {
        console.log(response);
    }

    changeLanguage(language: string): Promise<any> {
        if (!language) {
            return Promise.reject('No language specified');
        }
        return this.loadTextContent(language)
            .then((json) => {
                this.processContentJson(json);
                this.languageChangeEmitter.next(language); // emit event for text-outlet to update itself
            });
    }


    processContentJson(json: any) {
        let scenes: Array<any>;
        if (json.scenes instanceof Array) {
            scenes = json.scenes;
            scenes.forEach(scene => this.processSceneA(scene));
        }
    }

    processSceneA(scene: any) {
        if (scene.ID) {
            this.textContentA[scene.ID] = scene;
        } else {
            // general text content -
            Object.keys(scene).forEach(key => this.addGeneralKey(key, scene[key]));
        }
    }

    addGeneralKey(key: string, value: string) {
        if (key && value) {
            this.textContentA[GENERAL][key] = value;
        }
    }

    // getText(key: string = null, sceneId: string = GENERAL): string {
    //     return this.textContent[sceneId][key];
    // }

    // getTextForYear(rangeRef: string, yearRef: string, sceneId?: string): any {
    //     /* If year is specified - append range name with _R + YearNo */
    //     if (yearRef) {
    //         let year = this.calcService.getValue(yearRef);
    //         if (year) {
    //             rangeRef += '_R' + year;
    //         }
    //     }

    //     return this.getText(rangeRef, sceneId);
    // }

    getScene(sceneId: string): any {
        return this.textContentA[sceneId];
    }
    getText(key: string, sceneId: string = Constants.TEXT_ENGINE.GENERAL): string {
        // if (!this.isApiReady) {
        //     return;
        // }
        return this.textContentA[sceneId][key];
    }
    getMeetings(): any {
        const keys = {};
        for (const key in this.textContentA) {
            if (this.textContentA[key].PageType === 'SingleSelect') {
                keys[key] = this.textContentA[key];
            }
        }
        return keys;
    }

    getMessages(): any {
        const keys = {};
        for (const key in this.textContentA) {
            if (this.textContentA[key].PageType === 'Read Update') {
                keys[key] = this.textContentA[key];
            }
        }
        return keys;
    }

    getInitiatives(initiativeKey: string = 'Initiative'): any {
        const keys = {};
        for (const key in this.textContentA) {
            if (this.textContentA[key].PageType === initiativeKey) {
                keys[key] = this.textContentA[key];
            }
        }
        return keys;
    }
    getEvents(): any {
        let scenes: Array<any> = [];
        for (const key in this.textContentA) {
            if (key.indexOf("Event") > -1) {
                scenes.push(this.textContentA[key]);
            }
        }
        return scenes;
    }
    getEventByName(Name) {
        let scenes: Array<any> = [];
        for (const key in this.textContentA) {
            if (key.indexOf("Event") > -1) {
                let sceneName = this.textContentA[key].Name;
                sceneName = sceneName.replace("&", "");
                sceneName = sceneName.replace("&", "");
                if (sceneName == Name)
                    return this.textContentA[key];
            }
        }
    }
    getDropDownOptions(pageID) {
        let scenes: Array<any> = [];
        for (const key in this.textContentA) {
            if (key.indexOf(pageID) > -1 && key.indexOf("Explanation Options") > -1) {
                scenes.push(this.textContentA[key]);
            }
        }
        return scenes;
    }
    getFeedback(): any {
        const keys = {};
        for (const key in this.textContentA) {
            if (this.textContentA[key].PageType === 'Feedback') {
                keys[key] = this.textContentA[key];
            }
        }
        return keys;
    }

    getVarianceOptionById(pageID) {
        let scenes: Array<any> = [];
        for (const key in this.textContentA) {
            if (key == pageID) {
                let scene = this.textContentA[key];
                for (const scenekey in this.textContentA[key]) {
                    if (scenekey != "ID") {
                        scenes.push(this.textContentA[key][scenekey]);
                    }
                }
                break;
            }
        }
        return scenes;
    }

    getSceneById(id) {
        const keys = {};
        for (const key in this.textContentA) {
            if (key == id) {
                keys[key] = this.textContentA[key];
            }
        }
        return keys;
    }

    getSingleSelectChoices(id: string): Array<any> {
        const localChoices = [];

        const localObj = this.getScene(id);
        const keyArr: any[] = Object.keys(localObj);

        let i = 1;
        let moreChoices = false;
        keyArr.forEach((key: any) => {
            if (key.indexOf('alt' + i) !== -1) {
                moreChoices = true;
            }
        });

        while (moreChoices) {
            let localLabel = '';
            let localDesc = '';
            let localFeedback = '';
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i) !== -1) && (key.length < 4 + i.toString().length)) {
                    localLabel = localObj[key];
                }
            });
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('narrative') !== -1)) {
                    localDesc = localObj[key];
                }
            });
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('feedback') !== -1)) {
                    localFeedback = localObj[key];
                }
            });
            localChoices.push({
                'value': String.fromCharCode(64 + i),
                'label': localLabel,
                'desc': localDesc,
                'feedback': localFeedback
            });
            i++;
            moreChoices = false;
            keyArr.forEach((key: any) => {
                if (key.indexOf('alt' + i) !== -1) {
                    moreChoices = true;
                }
            });
        }

        return localChoices;
    }

    getMultiSelectChoices(id: string): Array<any> {
        const localChoices = [],
            localObj = this.getScene(id),
            keyArr: any[] = localObj.keys();
        let i = 1,
            moreChoices = false;

        keyArr.forEach((key: any) => {
            if (key.indexOf('alt' + i) !== -1) {
                moreChoices = true;
            }
        });

        while (moreChoices) {
            let localLabel = '',
                localDesc = '',
                localRange = '',
                localFeedback = '';
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i) !== -1) && (key.length < 4 + i.toString().length)) {
                    localLabel = localObj.getValue(key);
                }
            });
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('narrative') !== -1)) {
                    localDesc = localObj.getValue(key);
                }
            });
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('range') !== -1)) {
                    localRange = localObj.getValue(key);
                }
            });
            keyArr.forEach((key: any) => {
                if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('feedback') !== -1)) {
                    localFeedback = localObj.getValue(key);
                }
            });
            localChoices.push({
                'label': localLabel,
                'desc': localDesc,
                'range': localRange,
                'feedback': localFeedback
            });
            i++;
            moreChoices = false;
            keyArr.forEach((key: any) => {
                if (key.indexOf('alt' + i) !== -1) {
                    moreChoices = true;
                }
            });
        }
        return localChoices;
    }

}
