import { Component, Input, OnInit, EventEmitter, ViewChild, AfterViewInit, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef, Output } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { TextService, CalcService, StorageService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'isma-singlewobbler',
  templateUrl: 'singlewobbler.component.html',
  styleUrls: ['./singlewobbler.component.styl']
})

export class SingleWobblerComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() wobblerData: any;
  isViewed: boolean = false;
  triggerVariable = '';
  decisionVariable = '';
  modelSubscriber: Subscription;
  @ViewChild('wobblerModal', { static: true }) public wobblerModal: ModalDirective;
  @Output() wobblerSubmitted = new EventEmitter();
  animate: boolean;

  constructor(private router: Router,
    private calcService: CalcService,
    private textService: TextEngineService,
    private storageService: StorageService,
    private communicatorservice: CommunicatorService,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.triggerVariable = this.wobblerData['wobblerTrigger'];    
    this.decisionVariable = this.wobblerData['wobblerRange'];
  }

  ngAfterViewInit() {
    //Adding it after view init as show wobbler method is only initialized after view init
    this.checkWobblerCondition();
  }

  unsubsribeCalcListener() {
    if (this.modelSubscriber) {
      this.modelSubscriber.unsubscribe();
    }
  }

  ngOnDestroy() {
    if (this.modelSubscriber) {
      this.modelSubscriber.unsubscribe();
    }
  }

  checkWobblerCondition() {
    let triggered = this.calcService.getValue(this.triggerVariable) != '1' ? false : true;
    this.isViewed = this.calcService.getValue(this.decisionVariable) != '0' ? true : false;
    // Listen to Model Change only if is has not been completed already
    if(!triggered){
      this.modelSubscriber = this.communicatorservice.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
          if(this.calcService.getValue(this.triggerVariable) == '1'){
            this.showWobbler();

            //No need to listen to model once it has already been triggered
            this.unsubsribeCalcListener()
          }
      });            
    }else if(triggered && !this.isViewed){
      // in case the wobbler has been triggered but the user missed it because offline or refresed the screen
      this.showWobbler();
    }
  }

  showWobbler() {
    let self = this;
    self.isViewed = self.calcService.getValue(self.wobblerData['wobblerRange']) === '0' ? false : true;

    if (this.isViewed) {
      // this.getAnswer();
      this.animate = true;
    }
    // this.wobbler = self.wobbler;
    self.wobblerModal.show();
  }

  hideWobbler(confirmed: boolean = false) {
    this.wobblerModal.hide();
  }

  onSubmitWobbler() {
    this.isViewed = true;
    this.animate = true;
    this.wobblerSubmitted.emit(this)
  }

  onContinueClick() {
    let range = this.wobblerData['wobblerRead'];
    if(range){
      this.calcService.setValue(range, 1);
    }
    this.hideWobbler();
  }
}
