import { Component, OnInit, ViewChild, TemplateRef, Output, EventEmitter } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

@Component({
  selector: 'ism-info-scenarios',
  templateUrl: './info-scenarios.component.html',
  styleUrls: ['./info-scenarios.component.styl']
})
export class InfoScenariosComponent implements OnInit {
    scenarioDataObj: Object;
    scenarioDataArray: Array<any>;
    activeScenario;
    activeScenarioIndex = 0;
    MAX_SCENARIO;
    isPinned = true;

    activeStep = 0;
    @ViewChild('InfoScenario', {static: false}) InfoScenario: TemplateRef<any>;
    @ViewChild('InfoScenarioRef', {static: false}) InfoScenarioRef: CommonModalComponent;
    @Output() onSubmit = new EventEmitter();
  
  
    constructor(
      private textEngineService: TextEngineService
    ) { }
  
    ngOnInit() {
      this.scenarioDataObj = this.textEngineService.getInitiatives('InfoScenario')
      this.scenarioDataArray = Object.keys(this.scenarioDataObj).map((key) => this.scenarioDataObj[key]);
      this.scenarioDataArray.forEach((obj, index) =>{
        const choices = this.textEngineService.getSingleSelectChoices(obj['ID'])
        obj['choices'] = choices
      })
      this.MAX_SCENARIO = this.scenarioDataArray.length;
      this.setActiveScenarrio();
    }
  
    setActiveScenarrio(){
      this.activeScenario = this.scenarioDataArray[this.activeScenarioIndex];
    }
  
    nextScenario(){
      if(this.activeScenarioIndex == this.MAX_SCENARIO - 1){
        this.activeScenarioIndex = 0
      }else{
        this.activeScenarioIndex++;
      }
  
      this.setActiveScenarrio();
    }
  
    prevScenario(){
      if(this.activeScenarioIndex == 0){
        this.activeScenarioIndex = this.MAX_SCENARIO - 1;
      }else{
        this.activeScenarioIndex--;
      }
  
      this.setActiveScenarrio();
    }
  
    togglePinState(){
      this.isPinned = !this.isPinned;
    }
  
    expandScenario(index){
      if(this.activeScenarioIndex != index){
        this.activeScenarioIndex = index;
        this.activeStep = 0
      }
      this.setActiveScenarrio();
      
      this.InfoScenarioRef.openModal(this.InfoScenario)
    }

    submitData(){
      this.onSubmit.emit();
    }
  
}
