import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { DataService } from 'src/app/shared/dataService';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { EVENTS, EXPORT_PDF } from 'src/app/utils/constants';

@Component({
  selector: 'ism-pdf-gen',
  templateUrl: './pdf-gen.component.html',
  styleUrls: ['./pdf-gen.component.styl']
})
export class PdfGenComponent implements OnInit {

  constructor(
    private textEngineService: TextEngineService, 
    protected calcService: CalcService,
    private communicator: CommunicatorService, 
    private dataService: DataService
  ) { }

  header=[];
  header1=[];
  header2=[];
  header3=[];
  header4=[];
  header5=[];
  header6=[];
  tableData1=[];
  tableData2=[];
  tableData3=[];
  tableData4=[];
  tableData5=[];
  tableData6=[];
  metricHeader=[];
  metricData=[];

  metricHeaderData = 'metricHeader';
  className;
  notesPresent;

  ngOnInit() {
    this.getData();

    this.communicator.getEmitter(EXPORT_PDF).subscribe((res)=>{
      this.getPdf();
      setTimeout(() => {
        window.print();
      }, 2000);
    })  
  }

  getData(){
    this.header=[
      {id:2, name: this.textEngineService.getText('columnpnllabe2')},
      {id:3, name: this.textEngineService.getText('scenarioComparison1')},
      {id:4, name:this.textEngineService.getText('scenarioComparison2')},
      {id:5, name:this.textEngineService.getText('scenarioComparison3')}

    ];

    this.metricHeader=[
      {id:1, name: this.textEngineService.getText('metricHeader')},
      {id:2, name: ''},
      {id:3, name: this.textEngineService.getText('scenarioComparison1')},
      {id:4, name:this.textEngineService.getText('scenarioComparison2')},
      {id:5, name:this.textEngineService.getText('scenarioComparison3')}
    ]

    this.header1=[
      {id:1, name: this.textEngineService.getText('rowPnllabel7')},
      ...this.header
    ];
    this.header2=[
      {id:1, name: this.textEngineService.getText('rowPnllabel10')},
      ...this.header
    ];

    this.header3=[
      {id:1, name: this.textEngineService.getText('rowPnllabel17')},
      ...this.header
    ];

    this.header4=[
      {id:1, name: this.textEngineService.getText('rowPnllabel22')},
      ...this.header
    ];

    this.header5=[
      {id:1, name: this.textEngineService.getText('rowPnllabel26')},
      ...this.header
    ];

    this.header6=[
      {id:1, name: this.textEngineService.getText('rowPnllabel27')},
      ...this.header
    ];

    this.tableData1 = this.dataService.getScenarioAccordian1();
    this.tableData2 = this.dataService.getScenarioAccordian2();
    this.tableData3 = this.dataService.getScenarioAccordian3();
    this.tableData4 = this.dataService.getScenarioAccordian4();
    this.tableData5 = this.dataService.getScenarioAccordian5();
    this.tableData6 = this.dataService.getScenarioAccordian6();
    this.metricData = this.dataService.getMetricData();
  }

  getPdf(){
    let className =[];

    if(this.calcService.getValue('tlInputSaveScenario1') =='0'){
      className.push('hideScenario1')
    }
    if(this.calcService.getValue('tlInputSaveScenario2') =='0'){
      className.push('hideScenario2')
    }
    if(this.calcService.getValue('tlInputSaveScenario3') =='0'){
      className.push('hideScenario3')
    }

    this.className = className.join(' ');


    //Check if any notes are present
    this.notesPresent = this.calcService.getValue('tlInputReportNotesScenario') == '0' ? false : true
  }

}
