import { Component, OnInit, Output, EventEmitter, ViewChild, Input } from '@angular/core';
import { Router } from '@angular/router';
import { CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { PasswordComponent } from 'src/app/shared/password-popup/password.component';
import { TIMER } from 'src/app/utils/constants';
import { CountdownComponent } from 'ngx-countdown';
import { Observable, interval } from 'rxjs';
import { take } from 'rxjs/operators';

@Component({
  selector: 'ism-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.styl']
})
export class TimerComponent implements OnInit {

  constructor(private router: Router, private storageService: StorageService, private communicator: CommunicatorService) { }

  @Output() onTimeUp = new EventEmitter();
  @ViewChild('countdown', { static: false }) public counter: CountdownComponent;

  @Input() timerValue;
  @Input() constant;


  ngOnInit() {
    this.getTimerValue();
  }


  handleEvent(event) {
    if (event.action == 'start') {
      this.storageService.setValue(this.constant + TIMER.TIMER_STARTED, 'true');
      this.getCurrentTimerValue();
    }

    if (event.action === 'notify') {
      setTimeout(() => {
        this.onTimeUp.emit()
      }, 1000);
    }

    if (event.action === 'done') {
      this.storageService.setValue(this.constant + TIMER.TIMER_COMPLETE, 'true');
    }

  }

  getCurrentTimerValue() {
    const numbers = interval(1000);

    const takeFourNumbers = numbers.pipe(take(this.timerValue));
    takeFourNumbers.subscribe(x => {
      let value = this.counter.i.value / 1000
      if (value >= 0) {
        this.storageService.setValue(this.constant + TIMER.TIME_REMAINING, JSON.stringify(value));
      }
    });
  }


  getTimerValue() {
    this.storageService.getValue(this.constant + TIMER.TIMER_COMPLETE).then((res) => {
      if (res == 'true') {
        this.timerValue = '0'
        this.onTimeUp.emit();
      }
    })

    this.storageService.getValue(this.constant + TIMER.TIME_REMAINING).then((res) => {
      if (res && res.valueOf() > 0) {
        this.timerValue = res.toString();
      }
    })
  }

}
