import { Component, OnInit } from '@angular/core';
import { HttpWrapperService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { AppConfig } from 'src/app/app.config';

@Component({
  selector: 'ism-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.styl']
})
export class LogoutComponent implements OnInit {

  constructor(private httpWrapper: HttpWrapperService, private router: Router, private appConfig: AppConfig) { }

  ngOnInit() {
    // pulse logout
    let logoutUrl = (this.appConfig.configuration.logoutUrl) ? (this.appConfig.configuration.logoutUrl) : '/Wizer/Wizer/Logout';
    this.httpWrapper.postJson(logoutUrl, null)
      .then(result => {
        // console.log('logged out from pulse ' + JSON.stringify(result))
        // clear all cookies
        // console.log('cookies before:'+ document.cookie)
        var cookies = document.cookie.split(';');
        for (var i = 0; i < cookies.length; i++) {
          var cookie = cookies[i].split('=')[0].trim();
          if (cookie.length > 0) {
            // console.log(cookie)
            document.cookie = cookie + '=; expires=Thu, 01 Jan 1970 00:00:00 UTC';
          }
        }
        setTimeout(() => {
          const url = this.appConfig.configuration.redirectAfterlogoutUrl;
          if(this.appConfig.configuration.redirectAfterlogoutUrl){
            window.location.href = url;
          }else{
            window.location.href = window.location.protocol + '//' + window.location.hostname + ((window.location.port) ? ':' + window.location.port : '') + '?eraseCache=true/';
          }
        }, 500);

      }).catch(() => {
        
      });
  }

}
