import { Component, OnInit, ViewChild, OnDestroy, TemplateRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CalcService, CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { DataService } from 'src/app/shared/dataService';
import { PasswordComponent } from 'src/app/shared/password-popup/password.component';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { EVENTS, EXPORT_PDF, TIMER, INITIALIZE_TIMER, SCENARIO_SUBMIT } from 'src/app/utils/constants';
import { Utils } from 'src/app/utils/utils';
import { Subscription } from 'rxjs';
import { ConfirmationPopup } from 'src/app/shared/confirmation-popup/confirmation-popup';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

@Component({
  selector: 'ism-dashboard-data',
  templateUrl: './dashboard-data.component.html',
  styleUrls: ['./dashboard-data.component.styl']
})


export class DashboardDataComponent implements OnInit, OnDestroy {
  routeObserver: Subscription;
  submitObserver: Subscription;
  isScenarioComparisonActive = false;
  isFinanceChallenge = false;

  @ViewChild('passwordModal', { static: false }) public passwordDashboard: PasswordComponent;
  passwordKeyText = ''
  passwordMsgText = ''

  scenariosUnlocked = false;
  scenariosCompleted = false;
  showSubmit = true;

  callback;
  confirmMsg = '';
  @ViewChild('confirmationPopUp', {static: false}) public confirmationPopUpRef: ConfirmationPopup;

  @ViewChild('thankYouPopup', {static: false}) public thankYouPopup: CommonModalComponent;
  @ViewChild('popupContent', {static: false}) public popupContent: TemplateRef<any>;

  constructor(private textEngineService: TextEngineService, protected calcService: CalcService, private utils: Utils,
    private communicator: CommunicatorService, private router: Router, private dataService: DataService,
    private storageService: StorageService) {

  }

  tabData = [
    { id: 1, name: this.textEngineService.getText('dashboardTab1'), url: 'pnl' },
    { id: 2, name: this.textEngineService.getText('dashboardTab2'), url: 'payroll' },
    { id: 3, name: this.textEngineService.getText('dashboardTab3'), url: 'scenario-comparison' },
  ];  

  scenarioRangeArray = [
    "tlOutputPnLScenarioTraffic",
    "tlOutputPnLScenarioConversion",
    "tlOutputPnLScenarioUTP",
    "tlOutputPnLScenarioAUR",
    "tlOutputPnLScenarioMerchandiseSales",
    "tlOutputPnLScenarioNonMerchandiseSales",
    "tlOutputPnLScenarioTotalRevenue",
    "tlOutputPnLScenarioMerchandiseCost",
    "tlOutputPnLScenarioShrink",
    "tlOutputPnLScenarioTotalMerchandiseCost",
    "tlOutputPnLScenarioMerchandiseMargin",
    "tlOutputPnLScenarioMerchandiseMarginRate",
    "tlOutputPnLScenarioDiscount",
    "tlOutputPnLScenarioOccupancy",
    "tlOutputPnLScenarioFreight",
    "tlOutputPnLScenarioDistribution",
    "tlOutputPnLScenarioTotalCOGS",
    "tlOutputPnLScenarioGrossProfit",
    "tlOutputPnLScenarioGrossMargin",
    "tlOutputPnLScenarioPayroll",
    "tlOutputPnLScenarioPayrollOther",
    "tlOutputPnLScenarioTotalPayroll",
    "tlOutputPnLScenarioNetAdvertising",
    "tlOutputPnLScenarioBankCardCharges",
    "tlOutputPnLScenarioOtherStoreExp",
    "tlOutputPnLScenarioTotalOtherExpenses",
    "tlOutputPnLScenarioTotalExpenses",
    "tlOutputPnLScenarioContribution",
    "tlOutputPnLScenarioContributionMargin",
  ];

  scenarioMetricRangeArray = [
    "tlOutputDBValueScenarioMetric1",
    "tlOutputDBValueScenarioMetric2",
    "tlOutputDBValueScenarioMetric3",
  ];

  resetScenarioMetricRangeArray = [
    'tlInputPnLTraffic',
    'tlInputPnLConversion',
    'tlInputPnLUTP',
    'tlInputPnLAUR',
    'tlInputMerchandiseCost',
    'tlInputShrink',
    'tlInputDiscount',
    'tlInputOccupancy',
    'tlInputFreight',
    'tlInputDistribution',
    'tlInputPayrollOther',
    'tlInputBankCardCharges',
    'tlInputOtherStoreExp',
    'tlInputPayrollPTLabor',
    'tlInputPayrollPTHours',
    'tlInputPayrollPTRate',
    'tlInputPayrollFTLabor',
    'tlInputPayrollFTHours',
    'tlInputPayrollFTRate'
  ];

  ngOnInit() {
    this.onPageLoad();

    this.onRouteChange();
    this.routeObserver = this.router.events.subscribe((val) => {
      if (val instanceof NavigationEnd) {
        this.onRouteChange();
      }
    });

    this.submitObserver = this.communicator.getEmitter(SCENARIO_SUBMIT).subscribe(()=>{
      this.submitData();
    })
  }

  ngOnDestroy(){
    if(this.routeObserver){
      this.routeObserver.unsubscribe()
    }
    if(this.submitObserver){
      this.submitObserver.unsubscribe()
    }
  }

  onRouteChange() {
    this.isScenarioComparisonActive = this.router.url.indexOf("/scenario-comparison") > -1 ? true : false;
    this.isFinanceChallenge = this.router.url.indexOf("/scenarios") > -1 ? true : false;

    //Scroll page to top on route change
    try{
      document.getElementsByClassName('router-outlet-container')[0].scrollTop = 0
    }catch(e){}
  }

  saveScenario(index) {
    this.calcService.setValue('tlInputSaveScenario' + index, '1');

    for (let i = 0; i < this.scenarioRangeArray.length; i++) {
      let value = this.calcService.getValue(this.scenarioRangeArray[i]);
      let rangeName = this.scenarioRangeArray[i].replace('tlOutputPnLScenario', 'tlOutputPnLScenario' + index);
      this.calcService.setValue(rangeName, value)
    }

    /***for dashboard metric****/
    for (let i = 0; i < this.scenarioMetricRangeArray.length; i++) {
      let value = this.calcService.getValue(this.scenarioMetricRangeArray[i]);
      let rangeName = this.scenarioMetricRangeArray[i].replace('tlOutputDBValueScenario', 'tlOutputDBValueScenario' + index);
      console.log(rangeName);
      this.calcService.setValue(rangeName, value)
    }

  }

  resetScenario(idx = null) {
    let resetArray;
    if(idx){
      resetArray = this.scenarioRangeArray.map((value)=>{
        return value.replace('tlOutputPnLScenario', 'tlOutputPnLScenario' + idx);
      }).concat(
        this.scenarioMetricRangeArray.map((value)=>{
          return value.replace('tlOutputDBValueScenario', 'tlOutputDBValueScenario' + idx);
        }).concat('tlInputSaveScenario'+idx)
      )
    }else{
      resetArray = this.resetScenarioMetricRangeArray
    }
    this.utils.resetInputs(resetArray);
  }

  submitData() {
    if(!this.scenariosUnlocked){
      //Competetive Scenarios has not been unloacked yet
      this.passwordKeyText = "ScenariosUnlockPassword"
      this.passwordMsgText = "ScenariosUnlockPasswordMsg"
    }else if(!this.scenariosCompleted){
      //Competetive Scenarios has not been submitted yet
      this.passwordKeyText = "ScenariosCompletionPassword"
      this.passwordMsgText = "ScenariosCompletionPasswordMsg"
    }else{
      //Will never come here as submit button is not avaible once both are completed
      return;
    }

    this.passwordDashboard.showModal();
  }

  onPasswordSubmit(){
    if(!this.scenariosUnlocked){
      //Competetive Scenarios has not been unloacked yet which means the password submitted must unlock competetive scenarios
      this.startCompetetiveScenarios(true)
      
    }else if(!this.scenariosCompleted){
      //Competetive Scenarios has not been submitted yet which means the password submitted must submit competetive scenarios
      this.finishCompetetiveScenarios()
    }else{
      //Will never come here as submit button is not avaible once both are completed
      return;
    }
  }

  startCompetetiveScenarios(startTimer = false) {
    this.addTab();
    this.storageService.setValue(EVENTS.SCENARIO_START, "true");
    this.calcService.setValue('tlInputInfoScenariosCompleted', 1);
    this.router.navigate(['/dashboard','scenarios']);
    this.scenariosUnlocked = true;

    this.communicator.trigger(INITIALIZE_TIMER, TIMER.SCENARIO_TIMER)
  }

  finishCompetetiveScenarios(){
    this.removeTab();
    this.storageService.setValue(EVENTS.SCENARIO_COMPLETE, "true");
    this.calcService.setValue('tlInputScenariosCompleted', 1);
    this.router.navigate(['/dashboard','pnl']);
    this.scenariosCompleted = true;
    this.showSubmit = false;
    this.communicator.trigger(INITIALIZE_TIMER, null)
    this.thankYouPopup.openModal(this.popupContent)
  }

  addTab() {
    this.tabData.push({
      id: this.tabData.length + 1,
      name: this.textEngineService.getText('dashboardTab4'),
      url: 'scenarios'
    });
  }

  removeTab(){
    if(this.tabData.length == 4){
      this.tabData.pop()
    }
  }

  onPageLoad() {
    this.storageService.getValue(EVENTS.SCENARIO_START).then((res) => {
      if (res == 'true') {
        this.scenariosUnlocked = true;
        this.addTab();
        this.storageService.getValue(EVENTS.SCENARIO_COMPLETE).then((res) => {
          if(res == "true"){
            this.scenariosCompleted = true;
            this.removeTab();
            this.showSubmit = false;
            this.communicator.trigger(INITIALIZE_TIMER, null)
          }else{
            this.communicator.trigger(INITIALIZE_TIMER, TIMER.SCENARIO_TIMER)
          }
        })
      }else{
        this.communicator.trigger(INITIALIZE_TIMER, TIMER.INFO_SCENRIO_TIMER);
      }
    })


    //Set correct password keys in case they appear on time-up
    if(!this.scenariosUnlocked){
      //Competetive Scenarios has not been unloacked yet
      this.passwordKeyText = "ScenariosUnlockPassword"
      this.passwordMsgText = "ScenariosUnlockPasswordMsg"
    }else if(!this.scenariosCompleted){
      //Competetive Scenarios has not been submitted yet
      this.passwordKeyText = "ScenariosCompletionPassword"
      this.passwordMsgText = "ScenariosCompletionPasswordMsg"
    }
  }

  downloadReport(){
    this.communicator.trigger(EXPORT_PDF);
  }

  confirm(messageKey = '',callback: Function, ...args){
    this.confirmMsg = messageKey;
    this.callback = callback.bind(this, ...args);
    this.confirmationPopUpRef.showAlert();
  }

  onConfirm(){
    if(this.callback){
      this.callback()
    }
  }

  onCancel(){
    this.callback = null;
  }

  isScenarioSaved(scenarioNo){
    let range = 'tlInputSaveScenario'+scenarioNo
    let val = this.calcService.getValue(range)
    return val == '1'
  }

  onCloseClicked(){
    this.thankYouPopup.closeModal();
  }
}
