
import { Component, Input, OnInit, ViewChild, OnDestroy, HostListener } from '@angular/core';
import { TextService, CalcService, SyncComponent, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
// import { FileSaver } from '../../utils/filesaver';
// import { Utils } from '../../utils/utils';
import { Subscription } from 'rxjs';
import { ImportExportComponent } from 'src/app/import-export/import-export.component';

const KEY_NUM_1 = 49; // used to change the mode

@Component({
    selector: 'isma-holdscreen',
    templateUrl: './hold-screen.component.html',
    styleUrls: ['./hold-screen.component.styl'],
})

export class HoldScreenComponent implements OnInit, OnDestroy {
    mode: number = 0;
    showLoader: boolean = false;
    showPasswordErrorMessage: boolean = false;
    actualPassword: string = '';
    password: string = '';
    subscriber: Subscription;
    currentRound: number;

    @ViewChild('importExport', { static: false }) importExportRef: ImportExportComponent;

    constructor(
        private calcService: CalcService,
        private textService: TextService,
        private router: Router,
        private communicator: CommunicatorService,
    ) { }

    @HostListener('document:keyup', ['$event'])
    onKeypress($event) {
        if ($event.keyCode == KEY_NUM_1 && $event.shiftKey && $event.ctrlKey) {
            this.mode = this.mode == 0 ? 1 : 0;
        }
    }

    ngOnInit() {
        let self = this;
        this.currentRound = +this.calcService.getValue('tlInputTeamRound');
        this.actualPassword = this.textService.getText('FacilitatorPassword') || 'bts123';
        this.mode = 0;
        this.subscriber = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
            self.onModelChange();
        });
    }

    onModelChange() {
        const self = this,
            Round = +this.calcService.getValue('tlInputTeamRound');

        if (this.currentRound != Round) {
            this.currentRound = Round;
            // this.utils.resetInputs(this.resetRangeNames, this.calcService, true).then(() => {
                //this.reInitialiseRangeRefs().then(res=>{
                this.nextRound();
                // })
            // });
        }
    }


    ngOnDestroy() {
        if (this.subscriber)
            this.subscriber.unsubscribe();
    }


    onPasswordSubmit() {
        if (this.password === this.actualPassword) {
            this.importExportRef.importBalances()
        } else {
            this.showPasswordErrorMessage = true;
        }
    }

    nextRound() {
        this.router.navigateByUrl('/intro');
    }

    exportData(){
        this.importExportRef.exportDecisions();
    }
}
