import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BootstrapService } from '../bootstrap.service';
import { StorageService, Constants, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { LoggerService } from '@btsdigital/pulseutilities';
import { EVENTS } from 'src/app/utils/constants';

@Component({
  selector: 'ism-splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.styl']
})
export class SplashComponent implements OnInit {

  private modelProgressSubscription: Subscription;
  progress = 0;

  constructor(private bootstraper: BootstrapService, private router: Router,
    private logger: LoggerService, private storageService: StorageService,
    private communicator: CommunicatorService) { }

  ngOnInit() {
    if (!this.bootstraper.appReady) {
      this.modelProgressSubscription = this.communicator
        .getEmitter(Constants.MODEL_LOAD_PROGRESS)
        .subscribe((progress) => this.progress = progress);

      this.bootstraper
        .init()
        .then(() => {
          this.appReady();
        });
    } else {
      this.appReady();
    }
  }

  private appReady() {
    this.logger.log('Model loaded');
    let returnUrl: string;
    this.ensureComplete(EVENTS.INTRO_COMPLETE).then((complete)=>{
      if(complete){
        this.ensureComplete(EVENTS.IDEA_HUNT_COMPLETE).then((complete)=>{
          if(complete){
            this.ensureComplete(EVENTS.MOMENTS_COMPLETE).then((complete)=>{
              if(complete){
                this.storageService.getValue(Constants.RETURN_URL)
                .then(val => {
                  returnUrl = ((val) ? val : '/dashboard') + '';
                  this.router
                    .navigateByUrl(returnUrl);
                })
                .catch(err => {
                  this.router.navigateByUrl('/intro');
                });
              }else{
                this.router.navigateByUrl('/moments')
              }
            })
          }else{
            this.router.navigateByUrl('/idea-hunt')
          }
        })
      }else{
        this.router.navigateByUrl('/intro')
      }
    })
  }

  ensureComplete(key) {
    return this.storageService.getValue(key).then((value) => {
      if (value == "true") {
        return true
      } else {
        return false
      }
    })
  }
}
