import { Component, OnInit, Input, HostListener, ViewEncapsulation, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { CalcService, CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { INITIALIZE_TIMER, EVENTS, TIMER, FORCE_PASSWORD_POPUP, ZONE_INFO } from 'src/app/utils/constants';

@Component({
  selector: 'ism-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.styl']
})
export class HeaderComponent implements OnInit, OnDestroy {
  @Input() showDashboardMetrics = true;
  timerStorageValue;
  timerValue;
  isTimerActive = false
  routeObserver: Subscription;
  hideLogout = false;
  zone;
  zoneName = 'World 5, South Atlantic';

  TIMER_VALUE = {
    TIMER1: null,
    TIMER2: null,
    TIMER3: null,
    TIMER4: null
  }

  constructor(
    private calcService: CalcService, 
    private storageService: StorageService, 
    private textHelper: TextEngineService,
    private communicator: CommunicatorService,
    private router: Router,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.zone = window.localStorage.getItem('ManifestHost') // https://dsgmlc.btspulse.com/dsg1
    if(this.zone){
      this.hideLogout = true;
      try{
        this.zone = this.zone.slice(this.zone.lastIndexOf('/') + 1);
        this.zoneName = ZONE_INFO[this.zone];
      }catch(e){}
    }
    this.TIMER_VALUE.TIMER1 = +this.textHelper.getText("IdeaHuntTimer")
    this.TIMER_VALUE.TIMER2 = +this.textHelper.getText("MomentsTimer")
    this.TIMER_VALUE.TIMER3 = +this.textHelper.getText("DashboardTimerOne")
    this.TIMER_VALUE.TIMER4 = +this.textHelper.getText("DashboardTimerTwo")

    this.communicator.getEmitter(INITIALIZE_TIMER).subscribe((value)=>{
      this.isTimerActive = false;
      this.cdRef.detectChanges();

      this.initializeTimer(value)
    })

  }

  ngOnDestroy(){
    if(this.routeObserver){
      this.routeObserver.unsubscribe()
    }
  }

  // initializeTimer() {
  //   let URL = this.router.url
  //   if(URL.indexOf('/idea-hunt') != -1){
  //     this.updateTimer(EVENTS.IDEA_HUNT_COMPLETE, TIMER_VALUE.TIMER1, TIMER.IDEA_HUNT_TIMER)
  //   }

  //   if(URL.indexOf('/moments') != -1){
  //     this.updateTimer(EVENTS.MOMENTS_COMPLETE, TIMER_VALUE.TIMER2, TIMER.MOMENTS_TIMER)
  //   }

  //   if(URL.indexOf('/dashboard') != -1){
  //     //Check for first
  //     this.updateTimer(EVENTS.SCENARIO_START, TIMER_VALUE.TIMER3, TIMER.INFO_SCENRIO_TIMER).then(()=>{
  //       if(!this.isTimerActive){
  //         this.updateTimer(EVENTS.SCENARIO_COMPLETE, TIMER_VALUE.TIMER4, TIMER.SCENARIO_TIMER)
  //       }
  //     })
  //   }
  // }

  initializeTimer(key) {
    switch(key){
      case TIMER.IDEA_HUNT_TIMER:
        this.updateTimer(EVENTS.IDEA_HUNT_COMPLETE, this.TIMER_VALUE.TIMER1, TIMER.IDEA_HUNT_TIMER)
      break;
      case TIMER.MOMENTS_TIMER:
        this.updateTimer(EVENTS.MOMENTS_COMPLETE, this.TIMER_VALUE.TIMER2, TIMER.MOMENTS_TIMER)
      break;
      case TIMER.INFO_SCENRIO_TIMER:
        this.updateTimer(EVENTS.SCENARIO_START, this.TIMER_VALUE.TIMER3, TIMER.INFO_SCENRIO_TIMER)
      break;
      case TIMER.SCENARIO_TIMER:
        this.updateTimer(EVENTS.SCENARIO_COMPLETE, this.TIMER_VALUE.TIMER4, TIMER.SCENARIO_TIMER)
      break;
      case null:
      default:
        this.isTimerActive = false;
    }
  }

  updateTimer(CompletionStorageKey, timerValue, timerConstant){
    return this.storageService.getValue(CompletionStorageKey).then((response)=>{
      if(response == "true"){
        // Completed
        this.isTimerActive = false;
      }else{
        // Not Completed
        this.timerValue = timerValue;
        this.timerStorageValue = timerConstant
        this.isTimerActive = true;
      }
    })
  }


  showPopUpOnTimer(){
    this.communicator.trigger(FORCE_PASSWORD_POPUP)
  }
  
}
