import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { PasswordComponent } from 'src/app/shared/password-popup/password.component';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { EVENTS, TIMER, INITIALIZE_TIMER } from 'src/app/utils/constants';

const KEY_NUM_2 = 50;

@Component({
  selector: 'ism-idea-hunt',
  templateUrl: './idea-hunt.component.html',
  styleUrls: ['./idea-hunt.component.styl']
})
export class IdeaHuntComponent implements OnInit {
  ideaHuntData;
  isSubmitted = false;
  @ViewChild('passwordModal', {static: false}) public password: PasswordComponent;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_2) {
      this.toggleDisableState();
    }
  }

  constructor(private textEngineService: TextEngineService,
    protected calcService: CalcService,private communicator: CommunicatorService,private router: Router,
    private storageService: StorageService) {}


  private subscription: Subscription;

  ngOnInit() {
    this.getData();   

  }

  ngOnDestroy() {
    // this.subscription.unsubscribe();
  }

  getData(){
    let ideaHuntDataObj = this.textEngineService.getInitiatives('IdeaHunt')
    this.ideaHuntData =  Object.keys(ideaHuntDataObj).map((key) => ideaHuntDataObj[key]);

    this.storageService.getValue(EVENTS.IDEA_HUNT_COMPLETE).then((isIdeaHuntCompleted)=>{
      if(isIdeaHuntCompleted == "true"){
        this.isSubmitted = true
        this.communicator.trigger(INITIALIZE_TIMER, null)
      }else{
        this.isSubmitted = false;
        this.communicator.trigger(INITIALIZE_TIMER, TIMER.IDEA_HUNT_TIMER)
      }
    })

  }

  navigateByUrl(){
    if(!this.isSubmitted){
      this.storageService.setValue(EVENTS.IDEA_HUNT_COMPLETE, "true");
      this.calcService.setValue('tlInputIdeaHuntCompleted', 1);
    }
    this.router.navigate(['moments']);
  }

  submitData(){
    this.password.showModal();
    
  }

  toggleDisableState(){
    if(this.isSubmitted){
      this.isSubmitted = false;
    }
  }

}
